@use 'base/mixins';

// Base
@use 'base/layout';

// App font: load your font
@use 'base/fonts/helvetica-neue' as font;
@use 'base/fonts/inter' as font2;

@use 'base/typography' with (
  $font-family: font.$font-family,
  $font-family2: font2.$font-family,
);

// animations
@use 'base/_animations';

// Page layout: header, main, footer
@use 'base/default-layout';

@import '~/node_modules/swiper/swiper.css';
@import '~/node_modules/swiper/modules/navigation.css';
@import '~/node_modules/swiper/modules/pagination.css';

.swiper-slide {
  height: unset;
}