@use '../abstracts/variables' as var;
@use '../base/mixins' as mixin;

$font-family: Helvetica, sans-serif !default;
$font-family2: Helvetica, sans-serif !default;
$font-size: 10px !default;

html,
body {
  font-family: var.$font-family;
  font-size: 10px;
  color: var.$gray;
}

body {
  font-size: 1.8rem;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

.title-1 {
  font-size: 8rem;
  font-weight: 700;
  line-height: 1.2;
  color: var.$secondary;
  letter-spacing: -2px;

  @include mixin.small-laptop {
    font-size: 7rem;
  }

  @include mixin.tablet {
    font-size: 6rem;
  }

  @include mixin.small-tablet {
    font-size: 5rem;
  }

  @include mixin.phone {
    font-size: 4rem;
  }
}

.title-2 {
  font-size: 4.8rem;
  font-weight: 700;
  line-height: 1.1;
  color: var.$secondary;

  @include mixin.small-laptop {
    font-size: 4.2rem;
  }

  @include mixin.tablet {
    font-size: 3.7rem;
  }

  @include mixin.small-tablet {
    font-size: 3.2rem;
  }
}

.title-3 {
  font-size: 3.2rem;
  font-weight: 700;
  line-height: 1.5;
  color: var.$secondary;

  @include mixin.wide-tablet {
    font-size: 2.8rem;
  }

  @include mixin.small-tablet {
    font-size: 2.4rem;
    line-height: 1.33;
  }
}

.title-4 {
  font-size: 2.4rem;
  font-weight: 500;
  line-height: 1.33;
  color: var.$secondary;

  @include mixin.wide-tablet {
    font-size: 2.2rem;
  }

  @include mixin.small-tablet {
    font-size: 2rem;
    line-height: 1.6;
  }
}

.title-5 {
  font-size: 2.2rem;
  font-weight: 700;
  line-height: 1.27;
  color: var.$secondary;
}

.title-6 {
  font-size: 1.8rem;
  font-weight: 500;
  line-height: 1.66;
  color: var.$secondary;
}

.subtitle-1 {
  font-size: 2.2rem;
  font-weight: 300;
  line-height: 1.45;
  color: var.$gray;
  letter-spacing: 0.3px;

  @include mixin.tablet {
    font-size: 1.8rem;
    line-height: 1.55;
  }
}

.subtitle-2 {
  font-size: 1.8rem;
  font-weight: 300;
  line-height: 1.66;
  color: var.$gray;
  letter-spacing: 0.33px;

  @include mixin.small-tablet {
    line-height: 1.55;
  }
}


:not(.visually-impaired) {
  .dark {
    .title-1 {
      color: var.$light;
    }

    .title-2 {
      color: var.$light;
    }
  }
}