@use '../abstracts/variables' as var;
@use '../base/mixins' as mixin;

:root {
  --bs-gutter-x: 17px
}

html {
  width: 100%;
  overflow-x: hidden;
}

body {
  width: 100%;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

