.main-container {
  // overflow-x: hidden;
}

.page {
  padding-top: 38px;

  @include mixin.small-laptop {
    padding-top: 28px;
  }

  @include mixin.tablet {
    padding-top: 14px;
  }

  &__title {
    margin-bottom: 24px;

    @include mixin.wide-tablet {
      margin-bottom: 32px;
    }
  }

  &--without-sub &__title {
    margin-bottom: 48px;

    @include mixin.wide-tablet {
      margin-bottom: 32px;
    }
  }

  &__subtitle {
    margin-bottom: 48px;

    @include mixin.small-tablet {
      margin-bottom: 32px;
    }

    a {
      color: inherit;

      &:hover {
        color: inherit;
        text-decoration: underline;
      }
    }
  }

  &__back-link {
    margin-bottom: 24px;
  }

  .breadcrumbs {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 38px;
    font-family: var.$font-family;
    font-size: 1.4rem;
    font-weight: 400;
    line-height: 1;
    color: var.$gray;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    @include mixin.small-laptop {
      margin-bottom: 28px;
    }

    @include mixin.tablet {
      margin-bottom: 14px;
    }

    a {
      color: var.$gray;

      &:hover,
      &:visited {
        color: var.$gray;
      }

      &:last-child {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }

    img,
    svg {
      display: block;
      width: 20px;
      height: 20px;
    }
  }
}

.container {
  overflow: unset;
}

.back-link {
  padding: 8px 16px;
  box-sizing: border-box;
  width: 193px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
  color: var.$secondary;
  font-family: var.$font-family;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0.5px;
  border: 1px solid var.$line;
  background: transparent;
  outline: none;

  &:hover {
    color: var.$secondary;
  }

  svg {
    width: 16px;
    height: 16px;
    transform-origin: center;
    transform: rotateZ(180deg);
  }
}

.e-btn {
  padding: 0;
  outline: none;
  background: transparent;
  border: none;
  cursor: pointer;
}

.badge {
  border-top-left-radius: 1.6rem;
  border-bottom-right-radius: 1.6rem;
  transition: 0.3s linear;

  &--small {
    border-top-left-radius: 1.2rem;
    border-bottom-right-radius: 1.2rem;
  }

  &--large {
    border-top-left-radius: 2rem;
    border-bottom-right-radius: 2rem;
  }

  &--big {
    border-top-left-radius: 2.4rem;
    border-bottom-right-radius: 2.4rem;
  }

  &--block {
    border-top-left-radius: 4rem;
    border-bottom-right-radius: 4rem;
  }

  &--empty {
    border: 1px solid var.$line;
    color: var.$light;
  }

  &--orange {
    background: var.$primary;
    color: var.$light;
  }

  &--blank {
    background: var.$tertiary;
    color: var.$gray2;
  }

  &--dark {
    background: var.$secondary;
    color: var.$light;

    &:hover,
    &:active {
      color: var.$light;
      background: var.$primary;
    }
  }

  &--light {
    background: var.$light;
    color: var.$secondary;

    &:hover,
    &:active {
      color: var.$light;
      background: var.$primary;
    }
  }
}

.dropdown {
  &__label {
    font-weight: 600;
  }
}

.dropdown-menu {
  min-width: 100%;
  box-sizing: border-box;
  border-radius: 12px;
  border: 1px solid var.$line;
  padding: 24px 32px;
  max-height: 250px;
  padding-right: 16px;
  min-width: 200px;

  &--full {
    max-height: unset;
  }

  &.show {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    overflow: hidden;
  }

  &__inner {
    flex-grow: 1;
    height: 100%;
    overflow: auto;
    padding-right: 16px;

    &::-webkit-scrollbar {
      width: 3px;
      border-radius: 1.5px;
    }

    &::-webkit-scrollbar-track {
      background: #E9ECF2;
      border-radius: 1.5px;
    }

    &::-webkit-scrollbar-thumb {
      background: #626A77;
    }

    &::-webkit-scrollbar-thumb:hover {
      background: #626A77;
    }
  }

  li {
    margin-bottom: 12px;
    cursor: pointer;

    &:last-child {
      margin-bottom: 0;
    }

    &:hover,
    &.active {
      color: var.$primary;

      a {
        color: var.$primary;
      }
    }
  }

  li,
  a {
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 1.66;
    color: var.$gray;
    transition: 300ms linear;

    &:hover {
      color: var.$primary;
    }

    @include mixin.tablet {
      font-size: 1.6rem;
    }
  }
}

.tabs {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 12px;

  a {
    padding: 12px 24px;
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 1.5;

    @include mixin.small-tablet {
      padding: 8px 16px;
    }
  }

  &--grab {
    @include mixin.small-tablet {
      flex-wrap: nowrap;
      overflow-y: hidden;
      scrollbar-width: none;

      &::-webkit-scrollbar {
        display: none;
      }
    }
  }
}

.pagination {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;

  @include mixin.phone {
    gap: 0;
  }

  &__btn {
    width: 48px;
    height: 48px;
    box-sizing: border-box;
    padding: 10px;
    border-radius: 8px;
    border: 1px solid #F1F1F1;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: var.$secondary;
    font-family: var.$font-family;
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 1.5;
    transition: 0.3s linear;

    @include mixin.phone {
      border-radius: 0px;

      &:first-child {
        border-top-left-radius: 8px;
        border-bottom-left-radius: 8px;
      }

      &:last-child {
        border-top-right-radius: 8px;
        border-bottom-right-radius: 8px;
      }
    }

    &:hover,
    &--active {
      background: var.$secondary;
      border: 1px solid var.$secondary;
      color: var.$light;
    }

    svg {
      path {
        transition: 0.3s linear;
        fill: var.$secondary;
      }
    }

    &:hover svg,
    &--active svg {
      path {
        fill: var.$light;
      }
    }

    &--space {
      border-color: transparent;
      pointer-events: none;
    }

    &--right {
      svg {
        transform-origin: center;
        transform: rotateZ(180deg);
      }
    }
  }
}

.badge-loader {
  width: 100%;
  padding: 24px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
  height: 100%;

  &__title {
    color: var.$secondary;
    font-family: var.$font-family;
    font-size: 1.8rem;
    font-weight: 500;
    line-height: 1.66;
    margin-bottom: 7.2px;
    white-space: normal;
    text-align: left;
  }

  &__info {
    color: var.$gray;
    font-family: var.$font-family2;
    font-size: 1.4rem;
    font-weight: 400;
    line-height: 1.4;
    letter-spacing: 0.1px;
    text-align: left;
  }

  &__link {
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    background: rgba(var.$secondary, 0.10);
    border-radius: 50%;

    svg {
      width: 24px;
      height: 24px;
    }
  }
}

:not(.visually-impaired) {
  .dark {
    .page {
      .breadcrumbs {
        color: var.$light;

        a {

          &:hover,
          &:visited {
            color: var.$light;
          }
        }
      }
    }
  }
}

.block-filter {
  color: var.$gray2;

  &__dropdown-btn {
    display: flex;
    align-items: center;
    gap: 4px;
    padding: 8px 16px;
    box-sizing: border-box;

    span {
      color: var.$gray2;
      font-family: var.$font-family;
      font-size: 1.6rem;
      font-weight: 400;
      line-height: 1.5;
      letter-spacing: 0.5px;
    }

    svg {
      width: 16px;
      fill: var.$secondary;
    }
  }

  &__list {
    a {
      white-space: nowrap;
    }
  }
}

.feedback {
  cursor: pointer;
}

