@use '../abstracts/variables' as var;
@use '../base/mixins' as mixin;

.vision-control {
  background: #CCC;
  overflow: hidden;
  height: 0;

  .container {
    display: flex;
    align-items: stretch;
  }

  button {
    padding: 5px 15px;
    color: #000;
    font-weight: 700;

    &.active {
      background: #000;
      color: #FFF;
    }

    &.enlarge-size-btn-1 {
      font-size: 20px;
    }

    &.enlarge-size-btn-2 {
      font-size: 24px;
    }

    &.enlarge-size-btn-3 {
      font-size: 28px;
    }
  }
}

.visually-impaired {
  :not(.swiper *) {
    transition: unset !important;
  }

  body {
    filter: grayscale(1) contrast(1.5);
  }

  .hide-images {
    display: none !important;
  }

  .vision-control {
    height: auto;
  }

  &.enlarge-size-1 {
    .enlarge-size-btn-1 {
      background: #000;
      color: #FFF;
    }
  }

  &.enlarge-size-2 {
    font-size: 11px;

    .enlarge-size-btn-2 {
      background: #000;
      color: #FFF;
    }
  }

  &.enlarge-size-3 {
    font-size: 12px;

    .enlarge-size-btn-3 {
      background: #000;
      color: #FFF;
    }
  }
}

body {
  @include mixin.small-laptop {
    padding-top: 78px;
  }
}

.header {
  width: 100%;
  padding: 23.5px 0;

  @include mixin.small-laptop {
    padding: 17px 0;
    position: fixed;
    top: 0;
    z-index: 100;
    background: var.$light;
  }

  &__row {
    flex-wrap: nowrap;
  }

  &__row>* {
    width: unset;
  }

  &__logo {
    display: inline-block;
    max-width: 190px;
    width: 100%;
    position: relative;

    @include mixin.small-laptop {
      max-width: 135px;
    }

    img,
    svg {
      display: block;
      width: 100%;
    }

    &[data-type="beta"]:after {
      content: 'beta';
      position: absolute;
      left: 95%;
      top: 95%;
      font-size: 10px;
      color: #000;
      font-weight: 500;
    }
  }

  .nav {

    @include mixin.small-laptop {
      display: none;
    }

    &__list {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 24px;
    }

    &__link {
      display: flex;
      align-items: center;
      gap: 7.6px;
      color: var.$secondary;
      line-height: 1.66;
      transition: 0.5s linear;

      &:hover,
      &:active,
      &.active {
        color: var.$primary;
      }

      svg {
        margin-bottom: 2px;
        display: block;
        width: 8.89px;

        path {
          transition: 0.5s linear;
          fill: var.$secondary;
        }
      }

      &:hover svg,
      &:active svg,
      &.active svg {
        path {
          fill: var.$primary;
        }
      }
    }

    &__sub-list {
      max-height: unset;
    }

    &__sub-item {
      white-space: nowrap;
    }
  }

  &__control {
    display: flex;
    align-items: center;
    gap: 12px;

    @include mixin.small-laptop {
      gap: 19.28px;
    }
  }

  .mobile-nav {
    display: none;

    @include mixin.small-laptop {
      display: block;
    }

    &__btn {
      width: 32px;
      height: 32px;
      box-sizing: border-box;
      padding: 9.33px 4px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: stretch;

      span {
        display: block;
        height: 2px;
        background: var.$secondary;
        border-radius: 2px;
      }
    }

    &__body {
      display: none;
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 15;
      background: var.$secondary;

      &--active {
        display: block;
      }
    }

    .container {
      padding: 0 17px;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: stretch;
    }

    &__head {
      padding: 17px 7px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 41px;
    }

    &__logo {
      display: inline-block;
      max-width: 135px;
      width: 100%;

      img {
        display: block;
        width: 100%;
      }
    }

    &__close {
      position: relative;
      width: 32px;
      height: 32px;

      span {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 28px;
        height: 2px;
        border-radius: 2px;
        background: var.$light;
        pointer-events: none;

        &:first-child {
          transform: translate(-50%, -50%) rotateZ(45deg);
        }

        &:last-child {
          transform: translate(-50%, -50%) rotateZ(-45deg);
        }
      }
    }

    &__nav {
      flex-grow: 1;
      overflow: auto;
    }

    &__item {
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &__link {
      font-family: var.$font-family;
      font-weight: 500;
      font-size: 2.4rem;
      line-height: 1.33;
      color: var.$light;
      transition: 0.3s linear;
      display: flex;
      align-items: center;
      gap: 14.38px;

      &:hover,
      &--active {
        color: var.$primary;

        path {
          fill: var.$primary;
        }
      }

      svg {
        width: 11.12px;
        height: 6.39px;
        transform-origin: center;
        transition: 0.3s linear;
        transform: rotateZ(180deg);

        path {
          fill: var.$light;
        }
      }

      &.collapsed {
        svg {
          transform: rotateZ(0deg);
        }
      }
    }

    &__sub-item {
      margin-top: 12px;
    }

    &__sub-link {
      font-family: var.$font-family;
      font-weight: 300;
      font-size: 1.8rem;
      line-height: 1.55;
      color: var.$light;
      letter-spacing: 0.3px;
      transition: 0.3s linear;

      &:hover,
      &--active {
        color: var.$primary;
      }
    }

    &__footer {
      padding-top: 55px;
      padding-bottom: 36px;

      &-top {
        display: flex;
        align-items: center;
        gap: 24px;
        margin-bottom: 24px;

        &>a {
          color: var.$light;
          font-weight: 300;

          &:hover,
          &:visited {
            color: var.$light;
          }
        }
      }
    }

    .mobile-language {
      display: inline-block;

      &__btn {
        font-family: var.$font-family;
        font-weight: 300;
        font-size: 1.8rem;
        line-height: 1.55;
        color: var.$light;
        display: flex;
        align-items: center;
        gap: 6px;

        svg {
          width: 8.89px;
          height: 5.11px;

          path {
            fill: var.$light;
          }
        }
      }
    }

    &__socials {
      display: flex;
      align-items: center;
      gap: 24px;
      margin-bottom: 24px;

      a {
        display: block;

        path {
          transition: 0.3s linear;
        }

        &:hover {
          path {
            fill: var.$primary;
          }
        }
      }

      li:last-child {
        a:hover {
          path {
            fill: black;

            &:first-child {
              fill: var.$primary;
            }
          }
        }
      }
    }

    &__copyright {
      font-family: var.$font-family;
      font-weight: 300;
      font-size: 1.4rem;
      line-height: 1.43;
      color: var.$light;
    }
  }

  .language {
    @include mixin.small-laptop {
      display: none;
    }

    &__btn {
      padding: 11.45px 16px;
      box-sizing: border-box;
      height: 40px;
      display: flex;
      align-items: center;
      gap: 8.09px;
      font-size: 1.6rem;
      font-weight: 400;
      line-height: 1.5;
      color: var.$secondary;
      transition: 0.3s linear;

      img,
      svg {
        width: 24px;
        height: 24px;

        path {
          transition: 0.3s linear;
        }
      }

      &:hover {
        border: 1px solid var.$primary;
        color: var.$primary;

        path {
          fill: var.$primary;
        }
      }
    }
  }

  .state-symbols {
    display: flex;
    align-items: center;
    padding: 8px 16px;
    box-sizing: border-box;
    height: 40px;
    transition: 0.3s linear;

    @include mixin.small-laptop {
      border: none;
      padding: 0 2px;
    }

    img,
    svg {
      display: block;
      width: 24px;
      height: 20px;

      @include mixin.small-laptop {
        width: 32px;
        height: 26px;
      }

      path {
        transition: 0.3s linear;
      }
    }

    &:hover {
      border: 1px solid var.$primary;

      @include mixin.small-laptop {
        border: none;
      }
      
      path {
        fill: var.$primary;
      }
    }
  }

  .theme {
    padding: 8px 16px;
    box-sizing: border-box;
    height: 40px;
    transition: 0.3s linear;

    @include mixin.small-laptop {
      border: none;
      padding: 0;
    }

    svg {
      display: block;
      width: 25px;
      height: 25px;

      @include mixin.small-laptop {
        width: 32px;
        height: 32px;
      }

      path {
        transition: 0.3s linear;
      }
    }

    &:hover {
      border: 1px solid var.$primary;

      @include mixin.small-laptop {
        border: none;
      }
      
      path {
        stroke: var.$primary;
      }
    }
  }
}

.footer {
  padding-top: 120px;
  padding-bottom: 120px;
  border-top-left-radius: 150px;
  background: var.$secondary;
  color: var.$light;
  font-family: var.$font-family;

  @include mixin.small-laptop {
    padding-top: 80px;
    padding-bottom: 80px;
    border-top-left-radius: 80px;
  }

  @include mixin.small-tablet {
    padding-top: 48px;
    padding-bottom: 48px;
    border-top-left-radius: 0;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 48px 64px;
    margin-bottom: 24px;

    @include mixin.small-tablet {
      margin-bottom: 48px;
      flex-wrap: wrap;
    }
  }

  &__logo {
    display: block;
    max-width: 190px;
    margin-bottom: 32px;

    img {
      display: block;
      width: 100%;
    }
  }

  .socials {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 24px;

    a {
      display: block;

      path {
        transition: 0.3s linear;
      }

      &:hover {
        path {
          fill: var.$primary;
        }
      }
    }

    li:last-child {
      a:hover {
        path {
          fill: black;

          &:first-child {
            fill: var.$primary;
          }
        }
      }
    }
  }

  .contact {
    width: 100%;
    max-width: 911px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 48px;

    @include mixin.small-tablet {
      gap: 24px;
    }

    &__title {
      color: var.$light;
      margin-bottom: 16px;
    }

    &__subtitle {
      font-size: 1.4rem;
      font-weight: 300;
      line-height: 1.428;
      opacity: 0.8;
    }

    &__link {
      padding: 12px 16px;
      flex-grow: 1;
      max-width: 340px;
      font-size: 1.6rem;
      font-weight: 400;
      line-height: 1.5;
      flex-shrink: 0;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 48px 64px;

    @include mixin.small-tablet {
      flex-wrap: wrap;
    }
  }

  &__copyright {
    font-size: 1.4rem;
    font-weight: 300;
    line-height: 1.428;
    opacity: 0.8;

    @include mixin.small-tablet {
      order: 2;
    }
  }

  .footer-data {
    width: 100%;
    max-width: 911px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 24px;

    @include mixin.small-tablet {
      order: 1;
    }

    &__links {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 32px;

      a {
        font-size: 1.4rem;
        font-weight: 300;
        line-height: 1.428;
        color: var.$light;
        text-decoration: none;
        display: flex;
        align-items: center;
        gap: 6px;
        opacity: 0.8;
      }

      li:first-child {
        a {
          text-decoration: underline;
        }
      }
    }
  }
}

.slide-controls {
  display: flex;
  align-items: flex-end;
  gap: 12px;

  @include mixin.phone {
    display: none;
  }

  &__btn {
    display: flex;
    padding: 8px 16px;
    justify-content: center;
    align-items: center;
    border: 1px solid rgba(var.$secondary, 0.1);

    &--left {
      border-top-left-radius: 16px;
      border-bottom-right-radius: 16px;
    }

    &--right {
      border-bottom-left-radius: 16px;
      border-top-right-radius: 16px;
      transform-origin: center;
      transform: rotateZ(180deg);
    }
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(var.$secondary, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  padding: 17px;

  &--active {
    opacity: 1;
    pointer-events: unset;
  }

  &__content {
    border-radius: 8px;
    background: #FFFFFF;
    box-sizing: border-box;
    padding: 24px;
  }
}

.contact-form {
  position: relative;
  max-width: 362px;
  width: 100%;

  &__title {
    font-family: var.$font-family2;
    font-size: 1.8rem;
    font-weight: 500;
    line-height: 1.33;
    color: var.$secondary;
    margin-bottom: 24px;
  }

  &__body {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 24px;
    margin-bottom: 24px;
  }

  &__label {
    input {
      width: 100%;
    }
  }

  &__btns {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    flex-wrap: wrap;
    gap: 16px;

    @include mixin.phone {
      flex-direction: column;
    }
  }

  &__btn {
    flex-grow: 1;
    padding: 8px 16px;
    font-family: var.$font-family;
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 1.5;
    color: var.$secondary;

    &--accept {
      color: var.$light;
    }
  }

  &__msg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #FFF;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    display: none;

    &--active {
      display: flex;
    }

    svg {
      margin-bottom: 24px;
    }
  }

  &__msg-title {
    font-family: var.$font-family2;
    font-size: 1.8rem;
    font-weight: 500;
    line-height: 1.33;
    color: var.$secondary;
    margin-bottom: 24px;
  }

  &__msg-text {
    font-family: var.$font-family;
    font-size: 1.8rem;
    font-weight: 300;
    line-height: 1.33;
    color: var.$gray;
    margin-bottom: 24px;
    flex-grow: 1;
  }

  &__msg-btn {
    box-sizing: border-box;
    width: 100%;
    padding: 8px 16px;
    font-family: var.$font-family;
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 1.5;
    color: var.$light;
  }
}

.input {
  flex-grow: 1;

  &--big {
    width: 100%;

    input {
      width: 100%;
      max-width: unset !important;
    }
  }

  &__title {
    font-family: var.$font-family;
    font-size: 1.4rem;
    font-weight: 400;
    line-height: 1.285;
    color: var.$secondary;
    margin-bottom: 6px;
  }

  &__body {
    padding: 11px 10px;
    background: transparent;
    border: none;
    outline: none;
    border-bottom: 1px solid #DFE0E2;
    font-family: var.$font-family;
    font-size: 1.4rem;
    font-weight: 400;
    line-height: 1.285;
    color: var.$secondary;
    width: 100%;

    &::placeholder {
      font-family: var.$font-family;
      font-size: 1.4rem;
      font-weight: 400;
      line-height: 1.285;
      color: var.$gray;
    }
  }

  input {
    max-width: 169px;

    @include mixin.phone {
      max-width: unset;
    }
  }

  textarea {
    display: block;
    max-width: 362px;
    max-height: 250px;
    overflow: hidden;
    resize: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      width: 3px;
      border-radius: 1.5px;
    }

    &::-webkit-scrollbar-track {
      background: #E9ECF2;
      border-radius: 1.5px;
    }

    &::-webkit-scrollbar-thumb {
      background: #626A77;
    }

    &::-webkit-scrollbar-thumb:hover {
      background: #626A77;
    }
  }

  &__count {
    font-family: var.$font-family;
    font-size: 1.2rem;
    font-weight: 400;
    line-height: 1.5;
    color: var.$secondary;
    margin-top: 8px;
  }
}

.visually-impaired {
  .dark {
    .logotype {
      display: block;
    }

    .logotype-dark {
      display: none;
    }
  }
}

:not(.visually-impaired) {
  .dark {
    .header {
      .nav {
        &__link {
          color: var.$light;

          &:hover,
          &:active,
          &.active {
            color: var.$primary;
          }

          svg {
            path {
              fill: var.$light;
            }
          }

          &:hover svg,
          &:active svg,
          &.active svg {
            path {
              fill: var.$primary;
            }
          }
        }
      }

      .language {
        &__btn {
          color: var.$light;

          img,
          svg {
            path {
              fill: var.$light;
            }
          }
        }
      }

      .theme {
        svg {
          path {
            stroke: var.$light;
          }
        }
      }

      .mobile-nav {
        &__btn {
          span {
            background: var.$light;
          }
        }
      }
    }

    .logotype {
      display: none;
    }

    .logotype-dark {
      display: block;
    }

    .state-symbols {
      svg {
        path {
          fill: #FFF;
        }
      }
    }

    .footer {
      background: #111D37;
    }
  }
}

.dark {
  header {
    @include mixin.small-laptop {
      background: var.$secondary;
    }
  }
}

.bvi-active *:not(.bvi-panel *) {
  transition: unset !important;
}

.bvi-active {
  .mobile-nav {
    &__close {
      span {
        background: #000;
      }
    }
  }
}

.tooltip {
  max-width: 250px;
  white-space: wrap;

  &__label {
    margin-bottom: 5px;
  }

  &__title {
    font-weight: 500;
    margin-right: 5px;
  }
}